<template>
  <section>
    <div v-if="isLoading">
      <base-spinner></base-spinner>
    </div>
    <base-modal :show="!!error" color="is-danger" title="Verification Error" @close="handleError">{{ error }}</base-modal>
    <base-modal :show="success" color="is-success" title="Verification Succeeded" @close="handleSuccess">
      Congratulations, your account has been verified. You can now use Stormrose to its full extend.
    </base-modal>
    <div class="card" v-if="!accountIsVerified">
      <div class="card-header">
        <p class="card-header-title">
          Account Verification
        </p>
      </div>
      <div class="card-content content">
        <p>
          Your account has not yet been verified. Stormrose uses micro payments within the
          <a href="https://server.fseconomy.net/">FSEconomy Game World</a> to verify accounts. Please check your
          <a href="https://server.fseconomy.net/paymentlog.jsp">Payment Log</a> for such a micro payment. It was sent
          when your account was registered, and should look like this:
        </p>
        <figure class="image">
          <img src="../../assets/payment.png" alt="payment illustration"/>
        </figure>
        <p>
          Please use the FSEconomy Game World <a href="https://server.fseconomy.net/banksummary.jsp">banking page</a>
          to return this micro payment to the group it came from. This is part of an automated process so please:
        </p>
        <ul>
          <li>send it back in one transaction &amp; make sure the amount is correct</li>
          <li>select the destination group from the drop-down list that appears when you start typing the name</li>
          <li>include the verification token (alphanumeric token provided in the &ldquo;Comments&rdquo; field) in the
            Comments field.
          </li>
        </ul>
        <p>
          Once the transaction is completed, use the button below to confirm the transaction and complete the
          verification process.
        </p>
        <button class="button is-primary" @click="doVerify">Complete Verification</button>
      </div>
    </div>
    <div class="card" v-if="accountIsVerified">
      <div class="card-header">
        <p class="card-header-title">
          Account Status
        </p>
      </div>
      <div class="card-content content">
        <p>
          <span class="icon has-text-success"><svg-icon type="mdi" :path="tick" :size="24">&nbsp;</svg-icon></span>
          &nbsp;Your account has been verified.
        </p>
      </div>
    </div>
  </section>
</template>

<script>
import BaseSpinner from "@/components/ui/BaseSpinner";
import BaseModal from "@/components/ui/BaseModal";
import SvgIcon from '@jamescoyle/vue-icon';
import { mdiAccountCheck } from '@mdi/js';
export default {
  name: "AccountVerify",
  components: {
    BaseSpinner,
    BaseModal,
    SvgIcon
  },
  computed: {
    accountIsVerified() {
      return this.$store.getters['auth/isVerified'];
    },
    tick() {
      return mdiAccountCheck;
    }
  },
  data() {
    return {
      isLoading: false,
      error: null,
      success: false
    }
  },
  methods: {
    handleError() {
      this.error = null;
    },
    handleSuccess() {
      this.success = false;
    },
    async doVerify() {
      this.isLoading = true;
      try {
        await this.$store.dispatch('auth/verifyProfile');
        this.success = true;
      } catch (error) {
        this.error = error.message || 'Failed to verify your account, try later.';
      }
      this.isLoading = false;
    }
  }
}
</script>
