<template>
  <section>
    <div v-if="isLoading">
      <base-spinner></base-spinner>
    </div>
    <base-modal :show="!!error" color="is-danger" title="Email Update Error" @close="handleError">{{ error }}</base-modal>
    <base-modal :show="success" color="is-success" title="Email Update Succeeded" @close="handleSuccess">
      Your email address has been changed.
    </base-modal>
    <div class="card">
      <div class="card-header">
        <p class="card-header-title">
          Change Email Address
        </p>
      </div>
      <div class="card-content content">
        <form @submit.prevent="doSubmit">
          <div class="field is-horizontal">
            <div class="field-label is-normal">
              <label class="label" for="currentEmail">Current Email Address</label>
            </div>
            <div class="field-body">
              <div class="field">
                <div class="control is-expanded has-icons-left">
                  <input class="input" :class="{'is-danger': ko, 'is-success': ok}" id="currentEmail" type="email" v-model.trim="currentEmail" @keyup="checkMail">
                  <span class="icon is-small is-left">
                  <svg-icon type="mdi" :path="emailIcon">&nbsp;</svg-icon>
                </span>
                </div>
                <p class="help is-danger" v-if="ko">Email address does not match current address</p>
                <p class="help is-success" v-if="ok">Email address matches current address</p>
              </div>
            </div>
          </div>
          <div class="field is-horizontal">
            <div class="field-label is-normal">
              <label class="label" for="newEmail">New Email Address</label>
            </div>
            <div class="field-body">
              <div class="field">
                <div class="control is-expanded has-icons-left">
                  <input class="input" :class="{'is-danger': nko, 'is-success': nok}"  id="newEmail" type="email" v-model.trim="newEmail" @keyup="checkNewMail">
                  <span class="icon is-small is-left">
                  <svg-icon type="mdi" :path="emailIcon">&nbsp;</svg-icon>
                </span>
                </div>
                <p class="help is-danger" v-if="nko">Not a valid email address</p>
                <p class="help is-success" v-if="nok">Valid email address</p>
              </div>
            </div>
          </div>
          <div class="field is-horizontal">
            <div class="field-label">
              <!-- Left empty for spacing -->
            </div>
            <div class="field-body">
              <div class="field">
                <article class="message is-danger" v-if="!formIsValid">
                  <div class="message-body">
                    Please make sure to enter a valid email address in both fields. Neither of them can be empty.
                  </div>
                </article>
              </div>
            </div>
          </div>
          <div class="field is-horizontal">
            <div class="field-label">
              <!-- Left empty for spacing -->
            </div>
            <div class="field-body">
              <div class="field">
                <div class="control">
                  <button class="button is-primary">Submit</button>
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </section>
</template>

<script>
import BaseSpinner from "@/components/ui/BaseSpinner";
import BaseModal from "@/components/ui/BaseModal";
import SvgIcon from '@jamescoyle/vue-icon';
import {mdiAt} from '@mdi/js';
function emailIsValid (email) {
  return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)
}
export default {
  name: "AccountEmail",
  components: {
    BaseSpinner,
    BaseModal,
    SvgIcon
  },
  computed: {
    emailIcon() {
      return mdiAt;
    },
    profileEmail() {
      const profile = this.$store.getters['auth/profile'];
      return profile.email;
    }
  },
  data() {
    return {
      isLoading: false,
      success: false,
      error: null,
      currentEmail: '',
      newEmail: '',
      formIsValid: true,
      ko: false,
      ok: false,
      nko: false,
      nok: false
    };
  },
  methods: {
    handleError() {
      this.error = null;
    },
    handleSuccess() {
      this.success = false;
      this.newEmail = '';
      this.currentEmail = '';
      this.formIsValid = true;
      this.ko = false;
      this.ok = false;
      this.nok = false;
      this.nko = false;
    },
    checkMail() {
      if (this.currentEmail !== '') {
        if (this.currentEmail === this.profileEmail) {
          this.ko = false;
          this.ok = true;
        } else {
          this.ko = true;
          this.ok = false;
        }
      } else {
        this.ok = false;
        this.ko = false;
      }
    },
    checkNewMail() {
      if (this.newEmail === '') {
        this.nok = false;
        this.nko = false;
        return;
      }
      if (emailIsValid(this.newEmail)) {
        this.nok = true;
        this.nko = false;
      } else {
        this.nok = false;
        this.nko = true;
      }
    },
    async doSubmit() {
      if (this.ko || !this.ok || this.nko || !this.nok) {
        this.formIsValid = false;
        return;
      }
      this.isLoading = true;
      const actionPayload = {
        currentEmail: this.currentEmail,
        newEmail: this.newEmail
      }
      try {
        await this.$store.dispatch('auth/changeEmail', actionPayload);
        this.success = true;
      } catch (error) {
        this.error = error.message || 'Failed to update your email address, try later.';
      }
      this.isLoading = false;
    }
  }
}
</script>
