<template>
  <section>
    <div v-if="isLoading">
      <base-spinner></base-spinner>
    </div>
    <base-modal :show="!!error" color="is-danger" title="Email Update Error" @close="handleError">{{ error }}</base-modal>
    <div class="card">
      <div class="card-header">
        <p class="card-header-title">
          Email Notification
        </p>
      </div>
      <div class="card-content content">
        <div class="field is-horizontal">
          <div class="field-label">
            <label class="label" for="notify">Notification</label>
          </div>
          <div class="field-body">
            <div class="field">
              <div class="control">
                <label class="checkbox has-text-grey-dark">
                  <input type="checkbox" id="notify" :checked="accountWantsNotify" @change="toggleNotify">
                  Notify me via email about soon expiring contracts
                </label>
              </div>
            </div>
          </div>
        </div>
        <div class="field is-horizontal">
          <div class="field-label">
            <!-- Left empty for spacing -->
          </div>
          <div class="field-body">
            <div class="field">
              <p class="help has-text-grey-dark">
                <span class="has-text-weight-semibold">Please note:</span> There will be one email sent 3 days prior
                contract expiration. If several contracts expire in three days time, you will only receive one email,
                listing all of them.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import BaseSpinner from "@/components/ui/BaseSpinner";
import BaseModal from "@/components/ui/BaseModal";
export default {
  name: "AccountNotify",
  components: {
    BaseModal,
    BaseSpinner
  },
  computed: {
    accountWantsNotify() {
      return this.$store.getters['auth/wantsNotify'];
    }
  },
  data() {
    return {
      isLoading: false,
      error: null
    }
  },
  methods: {
    async toggleNotify() {
      this.isLoading = true;
      try {
        await this.$store.dispatch('auth/toggleNotify');
      } catch (error) {
        this.error = error.message || 'Failed to update your notification settings, try later.';
      }
      this.isLoading = false;
    },
    handleError() {
      this.error = null;
    }
  }
}
</script>
