<template>
  <teleport to="body">
    <div class="modal" :class="{'is-active': show}">
      <div class="modal-background" @click="signalClose"></div>
      <div class="modal-content">
        <article class="message" :class="color">
          <div class="message-header">
            <slot name="header">
              <p>{{ title }}</p>
            </slot>
            <button class="delete" aria-label="delete" @click="signalClose"></button>
          </div>
          <div class="message-body">
            <slot></slot>
          </div>
        </article>
      </div>
      <button class="modal-close is-large" aria-label="close" @click="signalClose"></button>
    </div>
  </teleport>
</template>

<script>
export default {
  name: "BaseModal",
  props: {
    show: {
      type: Boolean,
      required: true
    },
    title: {
      type: String,
      required: false
    },
    color: {
      type: String,
      required: false,
      default: null
    }
  },
  emits: ['close'],
  methods: {
    signalClose() {
      this.$emit('close');
    }
  }
}
</script>
