<template>
  <section>
    <div v-if="isLoading">
      <base-spinner></base-spinner>
    </div>
    <base-modal :show="!!error" color="is-danger" title="New Password Error" @close="handleError">
      <section v-html="error"></section>
    </base-modal>
    <base-modal :show="success" color="is-success" title="New Password Succeeded" @close="handleSuccess">
      Your password has been changed.
    </base-modal>
    <div class="card">
      <div class="card-header">
        <p class="card-header-title">
          Change Password
        </p>
      </div>
      <div class="card-content content">
        <form @submit.prevent="doSubmit">
          <div class="field is-horizontal">
            <div class="field-label is-normal">
              <label class="label" for="currentPassword">Current Password</label>
            </div>
            <div class="field-body">
              <div class="field">
                <div class="control is-expanded has-icons-left">
                  <input class="input" id="currentPassword" type="password" v-model.trim="currentPassword">
                  <span class="icon is-small is-left">
                  <svg-icon type="mdi" :path="passwordIcon">&nbsp;</svg-icon>
                </span>
                </div>
              </div>
            </div>
          </div>
          <div class="field is-horizontal">
            <div class="field-label is-normal">
              <label class="label" for="newPassword">New Password</label>
            </div>
            <div class="field-body">
              <div class="field">
                <div class="control is-expanded has-icons-left">
                  <input class="input" :class="{'is-danger': ko, 'is-success': ok}" id="newPassword" type="password"
                         v-model.trim="newPassword" @keyup="checkNewPw">
                  <span class="icon is-small is-left">
                  <svg-icon type="mdi" :path="passwordIcon">&nbsp;</svg-icon>
                </span>
                </div>
                <p class="help is-danger" v-if="ko">Passwords must match</p>
                <p class="help is-success" v-if="ok">Passwords do match</p>
              </div>
            </div>
          </div>
          <div class="field is-horizontal">
            <div class="field-label is-normal">
              <label class="label" for="confirmPassword">Confirm New Password</label>
            </div>
            <div class="field-body">
              <div class="field">
                <div class="control is-expanded has-icons-left">
                  <input class="input" :class="{'is-danger': ko, 'is-success': ok}" id="confirmPassword" type="password"
                         v-model.trim="confirmPassword" @keyup="checkNewPw">
                  <span class="icon is-small is-left">
                  <svg-icon type="mdi" :path="passwordIcon">&nbsp;</svg-icon>
                </span>
                </div>
                <p class="help is-danger" v-if="ko">Passwords must match</p>
                <p class="help is-success" v-if="ok">Passwords do match</p>
              </div>
            </div>
          </div>
          <div class="field is-horizontal">
            <div class="field-label">
              <!-- Left empty for spacing -->
            </div>
            <div class="field-body">
              <div class="field">
                <article class="message is-danger" v-if="!formIsValid">
                  <div class="message-body">
                    Please make sure to enter a password in all three fields. Neither of them can be empty.
                  </div>
                </article>
              </div>
            </div>
          </div>
          <div class="field is-horizontal">
            <div class="field-label">
              <!-- Left empty for spacing -->
            </div>
            <div class="field-body">
              <div class="field">
                <div class="control">
                  <button class="button is-primary">Submit</button>
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </section>
</template>

<script>
import BaseSpinner from "@/components/ui/BaseSpinner";
import BaseModal from "@/components/ui/BaseModal";
import SvgIcon from '@jamescoyle/vue-icon';
import {mdiLock} from '@mdi/js';

export default {
  name: "AccountPassword",
  components: {
    BaseSpinner,
    BaseModal,
    SvgIcon
  },
  computed: {
    passwordIcon() {
      return mdiLock;
    }
  },
  data() {
    return {
      isLoading: false,
      success: false,
      error: null,
      currentPassword: '',
      newPassword: '',
      confirmPassword: '',
      formIsValid: true,
      ko: false,
      ok: false
    };
  },
  methods: {
    handleError() {
      this.error = null;
    },
    handleSuccess() {
      this.success = false;
      this.currentPassword = '';
      this.newPassword = '';
      this.confirmPassword = '';
      this.formIsValid = true;
      this.ko = false;
      this.ok = false;
    },
    checkNewPw() {
      if (this.newPassword !== '' || this.confirmPassword !== '') {
        if (this.newPassword === this.confirmPassword) {
          this.ko = false;
          this.ok = true;
        } else {
          this.ko = true;
          this.ok = false;
        }
      } else {
        this.ko = false;
        this.ok = false;
      }
    },
    async doSubmit() {
      if (this.currentPassword === '' || this.newPassword === '' || this.confirmPassword === '' || this.newPassword !== this.confirmPassword) {
        this.formIsValid = false;
        return;
      }
      this.isLoading = true;
      const actionPayload = {
        currentPassword: this.currentPassword,
        newPassword: this.newPassword
      };
      try {
        await this.$store.dispatch('auth/changePassword', actionPayload);
        this.success = true;
      } catch (error) {
        this.error = error.message || 'Failed to change your password, try later.';
      }
      this.isLoading = false;
    }
  }
}
</script>
